.profile{
     display: flex;
     align-items: center;
     flex-direction: column;
     gap: 35px;
}
.profile img{
    margin-top: 50px;
}
.profile h1{
    text-align: center;
    width: 70%;
    font-size: 90px;
    font-weight: 600;
}
.profile h1 span{
    background: linear-gradient(270deg,#AF2AF7 10%,#ECF140 90%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.profile p{
    width: 50%;
    text-align: center;
    font-size: 24px;
    line-height: 40px;
}
.profile-action{
    display: flex;
    align-items: center;
    gap: 25px;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 50px;
}
.profile-connect{
    padding: 25px;
    border-radius: 50px;
    background: linear-gradient(270deg,#AF2AF7 -5.09%,#ECF140 106.28%);
    cursor: pointer;
}
.profile-resume{
    padding: 25px 75px;
    border-radius: 50px;
    border: 2px solid white;
    cursor: pointer;
    text-decoration: none;
    color: white;
}
.profile-connect:hover{
    border: 2px solid white;
}
.profile-resume:hover{
    border-color: #AF2AF7;
}
@media(max-width:768px){
    .profile img{
        margin-top: 100px;
        width: 220px;
    }
    .profile h1{
        margin-top: 50px;
        width: 100%;
        font-size: 35px;
    }
    .profile p{
        width: 80%;
        font-size: 20px;
        line-height: 32px;
        margin: 10px 0px;
    }
    .profile-action{
        gap: 15px;
        font-size: 16px;
        font-weight: 600;
        margin-bottom: 50px;
    }
    .profile-connect{
        padding: 20px 20px;
        border-radius: 50px;
    }
    .profile-resume{
        padding: 20px 30px;
        border-radius: 50px;
    }
}