.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 100px;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style:none;
    gap: 35px;
    font-size: 20px;
}
.nav-connect{
    padding: 20px 20px;
    border-radius: 50px;
    background: linear-gradient(267deg,#AF2AF7 0.36%,#ECF140 100.06%);
    font-size: 22px;
    cursor: pointer;
    transition: 0.5s;
}
.nav-connect:hover{
    transform: scale(1.05);
    transition: 0.5s;
    color:black;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
}
.nav-menu li img{
    display: flex;
    margin: auto;
}
.anchor-link{
    text-decoration: none;
    color: white;
}
.nav-mob-close{
    display: none;
}
.nav-mob-open{
    display: none;
}
@media(max-width:768px){
    .navbar{
        margin: 20px 10px;
    }
    .nav-connect{
        display: none;
    }
    .nav-mob-open{
        display: block;
        position: fixed;
        right: 80px;
    }
    .nav-mob-close{
        display: block;
        position: relative;
        top: 30px;
        left: 150px;
        width: 30px;
    }
    .nav-menu{
        position: fixed;
        flex-direction: column;
        align-items: start;
        top: 0px;
        right: -260px;
        gap: 35px;
        background-color: #0b7503;
        width: 260px;
        height: 100%;
        z-index: 2;
        transition: 0.5s;
    }
    .nav-menu li{
        font-size: 25px;
        padding-left: 50px;
        flex-direction: row;
        gap: 25px;
    }
}