.exp{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin:40px 120px;
}
.exp-title{
    position: relative;
}
.exp-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}
.exp-title img{
    position: absolute;
    bottom: -2cm;
    right: -1cm;
    z-index: -1;
}
.exp-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    margin-bottom: 40px;
}
.exp1{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px 50px;
    border: 1px solid white;
    border-radius: 20px;
    gap: 20px;
    transition: 0.3s;
}
.exp1 h2{
    font-size: 30px;
    font-weight: 700;
    background: linear-gradient(270deg ,rgb(210, 47, 210) 30%,rgba(255, 185, 9, 0.975) 100.02%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.exp1 h3{
    font-size: 30px;
    font-weight: 700;
    background: linear-gradient(270deg ,rgba(255, 185, 9, 0.975) 30%,rgb(210, 47, 210) 100.02%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.exp1 p{
    font-size: 20px;
    font-weight: 500;
}
.exp1:hover{
    background: linear-gradient(270deg ,rgba(253, 252, 219, 0.779) 30%,rgb(250, 232, 221) 100.02%);
    color:black;
    border: 2px solid black;
    border-radius: 20px;
    transform: scale(1.05);
    transition: 0.3s;
}
@media(max-width:768px){
    .exp{
        gap: 60px;
        margin: 50px 50px;
        align-items: start;
    }
    .exp-title h1{
        font-size: 45px;
        padding: 0;
    }
    .exp-title img{
        width: 120px;
        bottom: -1cm;
        right: -15px;
    }
    .exp-container{
        grid-template-columns: 1fr;
        gap: 40px;
        margin-bottom: 50px;
    }
    .exp1{
        padding: 10px 30px;
        border: 1px solid white;
        border-radius: 20px;
        gap: 18px;
        transition: 0.3s;
    }
    .exp1 h2{
        font-size: 22px;
    }
    .exp1 h3{
        font-size: 22px;
    }
    .exp1 p{
        font-size: 20px;
        font-weight: 400;
    }
}
