.skills{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;
}
.skill-title{
    position: relative;
}
.skill-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}
.skill-title img{
    position: absolute;
    bottom: -2cm;
    right: -2cm;
    rotate: 10deg;
    z-index: -1;
}
.about-skills{
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-left: 150px;
    margin-bottom: 70px;
}
.about-skill{
    display: flex;
    gap: 50px;
    align-items: center;
    transition: 0.3s;
}
.about-skill p{
    min-width: 150px;
    font-size: 26px;
    font-weight: 500;
}
.about-skill hr{
    outline: none;
    border: none;
    width: 50%;
    height:10px;
    border-radius: 50px;
    background: linear-gradient(264deg,orange -5.09%,purple 106.28%);
} 
.about-skill:hover{
    transform:scale(1.05);
    transition:0.3s;
}
.about-skill hr:hover{
    outline: auto;
    outline-color: rgb(89, 237, 79);
    outline-style: groove;
}
@media(max-width:768px){
    .skills{
        gap: 0px;
        margin: 50px 50px;
        align-items: start;
    }
.skill-title{
    gap: 50px;
    align-items: start;
}
.skill-title h1{
    padding: 0;
    font-size: 50px;
    align-items: start;
}
.skill-title img{
    width: 130px;
    bottom: -1cm;
    right: -20px;
}
.about-skills{
    gap: 40px;
    align-items: start;
    margin-left: 50px;
    margin-bottom: 50px;
}
.about-skill{
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: start;
    width: 120%;
}
.about-skill p{
    font-size: 20px;
}
.about-skill hr{
    width: 100%;
    height: 5px;
}
}