.education{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;
}
.education-title{
    position: relative;
}
.education-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}
.education-title img{
    position: absolute;
    bottom: -2cm;
    right: -1cm;
    rotate: 10deg;
    z-index: -1;
}
.education-format{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding: 45px;
    /* background-image: linear-gradient(to right top, #039b1a, #1d73b9de, #e17a63bc, #f75033, #a2d336); */
    border-radius: 10px;
    border: 2px solid white;
    transition: 0.4s;
}
.education-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap:40px;
    margin-bottom: 60px;
}
.education-format h3{
    font-size: 25px;
    font-weight: 800;
}
.education-format h2{
    font-size: 33px;
    font-weight: 800;
    background: linear-gradient(270deg,#AF2AF7 50%,#ECF140 100.06%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.education-format p{
    font-size: 20px;
    line-height: 30px;
    max-width: 400px;
}
@media(max-width:768px){
    .education{
        align-items: start;
        gap: 60px;
        margin: 50px 40px;
    }
    .education-title h1{
        padding: 0;
        font-size: 45px;
    }
    .education-title img{
        width: 130px;
        bottom: -1cm;
        right: -20px;
    }
    .education-container{
        grid-template-columns: 1fr;
        gap:50px;
        margin-bottom: 50px;
    }
    .education-format{
        gap: 20px;
        padding: 32px;
    }
    .education-format h3{
        font-size: 28px;
    }
    .education-format h2{
        font-size: 28px;
    }
    .education-format p{
        font-size: 20px;
        line-height: 32px;
        max-width: 300px;
    }
}