.footer{
    margin: 50px 170px;
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.footer-top{
    display: flex;
    justify-content: space-between;
}
.footer-top-left p{
    font-size: 20px;
    max-width: 400px;
}
.footer-top-right{
    display: flex;
    align-items: center;
    gap: 35px;
}
.footer-email-input{
    display: flex;
    gap: 30px;
    padding: 20px 30px;
    border-radius: 50px;
    background: #32323B;
    padding-right: 80px;
}
.footer-email-input input{
    outline: none;
    border: none;
    background: transparent;
    color:#a0a0a0;
    font-family: Outfit;
}
.footer-subscribe{
    font-size: 20px;
    padding: 22px 50px;
    border-radius: 50px;
    background: linear-gradient(264deg,#df8908 -5.09%,#b415ff 106.28%);
    cursor: pointer;
    transition: 0.3s;
}
.footer-bottom{
    display: flex;
    justify-content: space-between;
    font-size: 18px;
}
.footer-bottom-right{
    display: flex;
    gap: 50px;
    margin-bottom: 50px;
}
.footer-email-input:hover{
    border: 2px solid white;
}
.footer-subscribe:hover{
    transform: scale(1.1);
    transition: 0.3s;
}
@media(max-width:768px){
    .footer{
        margin: 40px 40px;
    }   
    .footer-top{
        flex-direction: column;
        gap: 40px;
    }
    .footer-bottom{
        display: flex;
        flex-direction: column-reverse;
        margin-bottom: 50px;
    }
    .footer-top-right{
        flex-direction: column;
        align-items: start;
        gap: 20px;
        width: 95%;
    }
    .footer-email-input{
        gap: 10px;
        padding: 20px 30px;
        border-radius: 50px;
        background: #32323B;
        padding-right: 50px;
    }
    .footer-email-input input{
        outline: none;
        border: none;
        background: transparent;
        color:#a0a0a0;
        font-family: Outfit;
    }
    .footer-subscribe{
        font-size: 15px;
        padding: 10px 30px;
        border-radius: 50px;
        background: linear-gradient(264deg,#df8908 -5.09%,#b415ff 106.28%);
        cursor: pointer;
        transition: 0.3s;
    }
}