.about{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;
}
.about-title{
    position: relative;
}
.about-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}
.about-title img{
    position: absolute;
    bottom: -2cm;
    right: -1cm;
    rotate: 10deg;
    z-index: -1;
}
.about-sections{
    display: flex;
    gap: 80px;
}
.about-right{
    display: flex;
    flex-direction: column;
    gap: 80px;
}
.about-para{
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-size: 25px;
    font-weight: 500;
}
.project-title{
    font-size: 25px;
    font-weight: 600px;
    text-align: center;
}
.project-grid{
    display: flex;
    width: 100%;
    text-align: center;
    justify-content:center;
    margin-bottom: 50px;
}
.about-project{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    transition: 0.5s;
}
.about-project h1{
    font-size: 22px;
    font-weight: 600px;
    padding-top: 30px;
    background: linear-gradient(264deg,orange -5.09%,purple 106.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.about-project h1:hover{
    background: linear-gradient(264deg,#40ff00 10%,rgb(109, 172, 255) 100%);
    border: white;
    cursor: pointer;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.about-project p{
    font-size: 15px;
    font-weight: 300px;
}
@media(max-width:768px){
    .about{
        gap: 50px;
        margin: 50px 50px;
        align-items: start;
    }
    .about-title h1{
        font-size: 45px;
        padding: 0;
    }
    .about-title img{
        width: 130px;
        bottom: -1cm;
        right: -20px;
    }
    .about-sections{
        flex-direction: column;
    }
    .about-left {
        display: none;
        }
    .about-para{
        font-size: 20px;
        line-height: 30px;
    }
    .about-skill{
        gap: 20px;
        align-items: center;
    }
    .about-skill p{
        font-size: 20px;
    }
    .about-skill hr{
        height: 6px;
    }
    .project-title{
        font-size: 15px;
        align-items: start;
    }
    .project-grid{
        flex-direction: column;
        width:100%;
        margin-bottom: 50px;
    }
    .about-projects{
        display: flex;
        flex-direction: column;
    }
    .about-project{
        gap: 20px;
    }
    .about-project h1{
        margin: 10px;
        font-size: 22px;
        font-weight: 500;
    }
    
}