.mywork{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 50px;
    margin: 40px 80px;
}
.mywork-title{
    position: relative;
}
.mywork-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}
.mywork-title img{
    position: absolute;
    bottom:-2cm ;
    right:-2cm ;
    z-index: -1;
}
.mywork-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap:60px;
    transition: 0.5s;
}
.mywork-container img{
    border: 3px solid white;
    border-radius: 10px;
    padding:5px;
    width: 300px;
    height: 200px;
    transition: 0.3s;
    cursor: pointer;
}
.mywork-container p{
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    background: linear-gradient(267deg,purple 34%,orange 102.2%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: 0.3s;
}
.mywork-container img:hover{
    border: 3px solid;
    border-top-color:purple ;
    border-bottom-color: orangered;
    border-left-color: chartreuse;
    border-right-color: crimson;
    transform: scaleX(1.05);
    transition: 0.3s;
}
.mywork-container:hover{
    transform: scale(1.05);
    transition: 0.5s;
}
@media(max-width:768px){
    .mywork{
        gap: 50px;
        margin: 50px 50px;
        align-items: start;
    }
    .mywork-title h1{
        padding: 0;
        font-size: 45px;
        font-weight: 600;
    }
    .mywork-title img{
        width: 130px;
        bottom: -1cm;
        right: -20px;
    }
    .mywork-container{
        padding-right: 0px;
        grid-template-columns: 1fr;
        gap: 50px;
        margin-bottom: 60px;
    }
    .mywork-container img{
        padding: 0px;
        width: 100%;
        height: 100%;
        margin-bottom: 10px;
    }
    .mywork-container p{
        font-size: 20px;
    }
    .mywork-container:hover{
        transform: scale(1.01);
        transition: 0.5s;
    }
}